import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

@observer
class UserInvites extends Component {
    async loadAll() {
		this.setState({ isLoading: true });
		await this.loadResults();
		this.setState({ isLoading: false });
    }

	loadResults = async () => {
		const { adminStore } = this.props.stores;
        await adminStore.getUserInviteResults();
	}

    componentDidMount() {
        this.loadAll();
    }

    render() {
        const { adminStore, userStore } = this.props.stores;
        const { inviteResults } = adminStore;
        return (
            <div class='col-12'>
                <h5>Competition results</h5>
                <div>
                    <table class='table table-striped table-sm'>
                        <thead>
                            <tr>
                                <th>Invite Date</th>
                                <th>Invitee</th>
                                <th>Invited</th>
                                <th>activities</th>
                                <th>last activity</th>
                                <th>teams</th>
                            </tr>
                        </thead>
                        <tbody>
                            {inviteResults && inviteResults.map(res => <>
                                <tr>
                                    <td>{util.isoDate(res.date)}</td>
                                    <td>{res.user.email} ({res.user.activities})</td>
                                    <td>
                                        {res.invitedUser.email}<br />
                                        <span class='ml-2 font-weight-lighter'>{res.invitedUser.username}</span>
                                        <span class='ml-2 font-weight-lighter'>{res.invitedUser.firstname} {res.invitedUser.lastname}</span>
                                    </td>
                                    <td>{res.invitedUser.activities}</td>
                                    <td>{res.invitedUser.lastActivityEpoch ? util.isoDate(res.invitedUser.lastActivityEpoch) : ''}</td>
                                    <td>{res.teams && res.teams.map(t => <>
                                        <span class='ml-3'>
                                            {t.name}
                                            <i class='fas fa-users text-muted ml-3' /> {t.members.length}
                                            <i class='fas fa-dog text-muted ml-3' /> {t.dogs.length}
                                            <i class='fas fa-running text-muted ml-3' /> {t.workouts.length}
                                        </span><br />
                                    </>)}</td>
                                </tr>
                            </>)}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default UserInvites;