import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import mu from '../../lib/musher-util';

function replaceImages(line, images) {
    function imageReplacer(match, p1, p2, p3, offset, string) {
        // console.log({ match, p1, p2, p3, offset, string })
        if (util.isObject(images[p1])) {
            return `<img src=${images[p1].s3MediumLink} class='img-fluid border rounded-lg float-right' style='width: ${p2 || 50}vw;' />`;
        }
        return '';
    }

    if (util.isString(line)) {
        return line.replace(/\{\{img\.(\d+)\s*(\d*)\}\}/g, imageReplacer);
    }
    return line;
}

function replaceLinks(line) {
    function linkReplacer(match, p1, p2, p3, offset, string) {
        // console.log({ match, p1, p2, p3, offset, string })
        return `<a href='${p2}'>${p1}</a>`;
    }

    if (util.isString(line)) {
        return line.replace(/\[([^\]]+)\]\(([^)]+)\)/g, linkReplacer);
    }
    return line;
}

function replaceTags(part, images, idx) {
    // <img src={news.images[idx + 1].s3MediumLink} class='img-fluid border rounded-lg float-right' style='width: 50vw;' />
    const htmlLines = mu.nl2br(part).map((line) => {
        return `${replaceLinks(replaceImages(line, images))} <br />`;
    });
    return htmlLines.join('');
}

@observer
class NewsDetail extends Component {
    render() {
        const { news } = this.props;
        const { userStore } = this.props.stores;
        const { isAdmin } = userStore;
        return (
            <>
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
                    {(news.images && news.images[0]) ? <img src={news.images[0].s3LargeLink} class='img-fluid border rounded-lg' /> : ''}
                    
                    <h1>{isAdmin && <i class={`fas fa-eye${news.active ? ' text-success' : '-slash text-danger'} float-right`} />} {news.title}</h1>
                    <div class='text-muted'>
                        <small>
                            {util.isoDate(news.createdDate)} - {news.tag}
                        </small>
                    </div>
                    <p class='clearfix'>
                        <strong>{news.ingress}</strong>
                    </p>
                    {mu.nl2p(news.body).map((part, idx) => (
                        <p 
                            class='clearfix'
                            dangerouslySetInnerHTML={{
                                __html: replaceTags(part, news.images, idx)
                            }}
                        />
                    ))}
                </div>
            </>
        );
    }
}

export default NewsDetail;
