	polylineOptions = () => {
		const range = util.range(MIN_INCLINE, MAX_INCLINE, INCLINE_STEP);
		const colors = range.map((e) => {
			const val = normalizeRange(e, MIN_INCLINE, MAX_INCLINE, 0, MAX_HSL_COLOR);
			const { r, g, b } = util.hsl2Rgb(MAX_HSL_COLOR - val, 100, 45);
			return { color: `rgb(${r}, ${g}, ${b})` };
		})
		return colors;
	}

	polylineOptionIdxFn = (latLng) => {
		const altThresholds = util.range(MIN_INCLINE, MAX_INCLINE, INCLINE_STEP);

		for (let i = 0; i < altThresholds.length; ++i) {
			if (latLng.meta <= altThresholds[i]) {
				return i;
			}
		}
		return altThresholds.length;
	}

	polylinetrackPointFactory = (data) => {
		const { gpxInfo } = this.props;
		const points = data.filter(e => e[0] && e[1] && e[2]).map((item, idx) => {
			const trkpt = L.latLng(item[1], item[0], item[2]);
			if (gpxInfo && gpxInfo.climbs) {
				trkpt.meta = gpxInfo.climbs[idx] || 0;
				if (trkpt.meta > MAX_INCLINE) {
					trkpt.meta = MAX_INCLINE;
				}
				if (trkpt.meta < MIN_INCLINE) {
					trkpt.meta = MIN_INCLINE;
				}
			} else {
				trkpt.meta = 0;
			}
			return trkpt;
		});
		return points;
	}

